* {
  font-family: "Poppins", serif;
  scroll-behavior: smooth;
}
.rotating-div {
  animation: rotate 5s linear infinite;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-words {
  display: inline-block;
  animation: slide ease-in-out 6s infinite;
}
@keyframes slide {
  0%,
  15% {
    transform: translateY(-3%);
  }
  20%,
  40% {
    transform: translateY(-22%);
  }
  40%,
  60% {
    transform: translateY(-42%);
  }
  60%,
  80% {
    transform: translateY(-63%);
  }
  80%,
  90% {
    transform: translateY(-85%);
  }
  100% {
    transform: translateY(-3%);
  }
}
.btn-block {
  transform: translate(0, -30px);
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
.card-container:hover .btn-block {
  transform: translate(0, 0px);
  opacity: 1;
}
.card-container:hover .center-div {
  width: 97%;
  height: 97%;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.gear {
  animation: rotate 4s linear infinite;
}
.btn {
  background: rgb(41, 34, 195);
  background: linear-gradient(
    to right,
    rgba(41, 34, 195, 1) 29%,
    rgba(253, 3, 3, 1) 100%
  );
  transition: all 1s ease-in-out;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}
.btn:hover {
  background: linear-gradient(
    to right,
    rgba(253, 3, 3, 1) 29%,
    rgba(41, 34, 195, 1) 100%
  );
}
.bg-img {
  background-image: url(https://curelogics.org/wp-content/uploads/2024/08/Property-1Default.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.banner-bg {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(https://curelogics.org/wp-content/uploads/2024/08/Rectangle-21.png);
}
.box-shadow {
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.scroll-container {
  display: flex;
  gap: 2rem;
  width: max-content;
}
.scroll-container:hover {
  animation-play-state: paused;
}
.bg-image {
  background-image: url("/images/image 77.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@keyframes fall {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fall-mobile {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fall {
  animation: fall 0.5s ease-in-out forwards;
}
.animate-fall-mobile {
  animation: fall-mobile 0.5s ease-in-out forwards;
}
.contact-img {
  background-image: url("/images/contactbg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@keyframes moveRight {
  0% {
    left: 50%;
    opacity: 80%;
  }
  100% {
    left: 0;
    opacity: 0;
  }
}
@keyframes moveLeft {
  0% {
    right: 50%;
    opacity: 80%;
  }
  100% {
    right: 0;
    opacity: 0;
  }
}
.animate-move-right {
  animation: moveRight 5s infinite ease-in;
}
.animate-move-left {
  animation: moveLeft 5s infinite ease-in;
}
@keyframes borderAnimation {
  0% {
    border-color: transparent;
  }
  50% {
    border-color: red;
  }
  100% {
    border-color: transparent;
  }
}
@keyframes border-radius-animation {
  0% {
